import React from 'react'
import { Window, WindowContent, Button, Toolbar, Divider, TextField, List, ListItem } from 'react95'
import { Frame, TitleBar, ThemeProvider } from '@react95/core'
import { Calculator as CalcIcon } from '@react95/icons'

export default function Calculator(props) {
  const [editOpen, setEditOpen] = React.useState(false)
  const [viewOpen, setViewOpen] = React.useState(false)
  const [helpOpen, setHelpOpen] = React.useState(false)

  return (
    <ThemeProvider>
      <Window id='calculator'>
        <TitleBar
          active
          icon={<CalcIcon />}
          title='Calculator'
          className='calc-title'
          height={50}
        >
          <TitleBar.OptionsBox title-options>
            <button>
              <TitleBar.Option className='title-_'>
                <small>_</small>
              </TitleBar.Option>
            </button>
            <button>
              <TitleBar.Option className='title-□'>
                <small>□</small>
              </TitleBar.Option>
            </button>
            <button>
              <TitleBar.Option className='title-X'>
                <small>X</small>
              </TitleBar.Option>
            </button>
          </TitleBar.OptionsBox>
        </TitleBar>
        <Toolbar id='calc-header'>
          <button
            variant='menu'
            size='sm'
            onClick={() => setEditOpen(!editOpen)}
            active={editOpen ? true : false}
            >Edit</button>
            {editOpen ? (
              <List
                style={{
                  position: 'absolute',
                  left: '0',
                  top: '100%',
                  zIndex: '9999'
                }}
                open={editOpen ? true : false}
                onClick={() => setEditOpen(false)}
              >
                <ListItem size='sm'>
                  <small>Copy</small>
                  <small>Ctrl+C</small>
                </ListItem>
                <ListItem size='sm'>
                  <small>Paste</small>
                  <small>Ctrl+V</small>
                </ListItem>
              </List>
            ) : undefined}
          <button
            variant='menu'
            size='sm'
            onClick={() => setViewOpen(!viewOpen)}
            active={viewOpen ? true : false}
          >View</button>
          {viewOpen ? (
            <List
              style={{
                position: 'absolute',
                left: '50px',
                top: '100%',
                zIndex: '9999'
              }}
              open={viewOpen ? true : false}
              onClick={() => setViewOpen(false)}
            >
              <ListItem size='sm'>Scientific</ListItem>
              <ListItem size='sm'>Standard</ListItem>
            </List>
          ) : undefined}
          <button
            variant='menu'
            size='sm'
            onClick={() => setHelpOpen(!helpOpen)}
            active={helpOpen ? true : false}
          >Help</button>
          {helpOpen ? (
            <List
              style={{
                position: 'absolute',
                left: '100px',
                top: '100%',
                zIndex: '9999'
              }}
              open={helpOpen ? true : false}
              onClick={() => setHelpOpen(false)}
            >
              <ListItem size='sm'>Help Topics</ListItem>
              <Divider />
              <ListItem size='sm'>About Calculator</ListItem>
            </List>
          ) : undefined}
        </Toolbar>
        <Divider />
        <TextField id='calc-display' value={`${props.number}.`} disabled />
        <WindowContent id='calc-window'>
          <div id='calc-memory'>
            <Frame h={40} />
            <Button
              onClick={() => props.setMemNum(0)}
            >MC</Button>
            <Button
              onClick={() => props.setNumber(props.memNum)}
            >MR</Button>
            <Button
              onClick={() => props.setMemNum(props.number)}
            >MS</Button>
            <Button
              onClick={() => props.setMemNum(prevNum => prevNum + props.number)}
            >M+</Button>
          </div>
          <div id='calc-clear'>
            <Button id='back'
              onClick={() => props.setNumber(prevNum => (prevNum - (prevNum % 10)) * 0.1)}
            >Back</Button>
            <Button id='clear-entry'
              onClick={() => props.setNumber(0)}
            >CE</Button>
            <Button id='clear'
              onClick={() => {
                props.setNumber(0)
                props.setFirstNum(0)
                props.setOperator(undefined)
              }}
            >C</Button>
          </div>
          <div id='calc-math'>
            <div id='calc-numpad'>
              <Button
                onClick={() => {
                  if(props.operator && props.firstNum) {
                    props.setNumber(0)
                  }

                  props.setNumber(prevNum => prevNum*10 + 7)
                }}
              >7</Button>
              <Button
                onClick={() => {
                  if(props.operator && props.firstNum) {
                    props.setNumber(0)
                  }

                  props.setNumber(prevNum => prevNum*10 + 8)
                }}
              >8</Button>
              <Button
                onClick={() => {
                  if(props.operator && props.firstNum) {
                    props.setNumber(0)
                  }

                  props.setNumber(prevNum => prevNum*10 + 9)
                }}
              >9</Button>
              <Button
                onClick={() => {
                  if(props.operator && props.firstNum) {
                    props.setNumber(0)
                  }

                  props.setNumber(prevNum => prevNum*10 + 4)
                }}
              >4</Button>
              <Button
                onClick={() => {
                  if(props.operator && props.firstNum) {
                    props.setNumber(0)
                  }

                  props.setNumber(prevNum => prevNum*10 + 5)
                }}
              >5</Button>
              <Button
                onClick={() => {
                  if(props.operator && props.firstNum) {
                    props.setNumber(0)
                  }

                  props.setNumber(prevNum => prevNum*10 + 6)
                }}
              >6</Button>
              <Button
                onClick={() => {
                  if(props.operator && props.firstNum) {
                    props.setNumber(0)
                  }

                  props.setNumber(prevNum => prevNum*10 + 1)
                }}
              >1</Button>
              <Button
                onClick={() => {
                  if(props.operator && props.firstNum) {
                    props.setNumber(0)
                  }

                  props.setNumber(prevNum => prevNum*10 + 2)
                }}
              >2</Button>
              <Button
                onClick={() => {
                  if(props.operator && props.firstNum) {
                    props.setNumber(0)
                  }

                  props.setNumber(prevNum => prevNum*10 + 3)
                }}
              >3</Button>
              <Button
                onClick={() => {
                  if(props.operator && props.firstNum) {
                    props.setNumber(0)
                  }

                  props.setNumber(prevNum => prevNum*10)
                }}
              >0</Button>
              <Button
                onClick={() => props.setNumber(prevNum => prevNum - (2*prevNum))}
              >+/-</Button>
              <Button>.</Button>
            </div>
            <div id='calc-ops'>
              <Button
                onClick={() => {
                  if(props.operator) {
                        props.setNumber(props.firstNum / props.number)
                      }

                  props.setFirstNum(props.number)
                  props.setOperator('division')
                }}
              >/</Button>
              <Button
                onClick={() => {
                  if(props.firstNum) {
                    props.setNumber(props.firstNum * props.number)
                  }

                  props.setFirstNum(props.number)
                  props.setOperator('multiplication')
                }}
              >*</Button>
              <Button
                onClick={() => {
                  if(props.operator) {
                        props.setNumber(props.firstNum - props.number)
                      }

                  props.setFirstNum(props.number)
                  props.setOperator('subtraction')
                }}
              >-</Button>
              <Button
                onClick={() => {
                  if(props.operator) {
                        props.setNumber(props.firstNum + props.number)
                      }

                  props.setFirstNum(props.number)
                  props.setOperator('addition')
                }}
              >+</Button>
              <Button
                onClick={() => props.setNumber(prevNum => {
                  console.log(prevNum)
                  console.log(props.operator)

                  switch(props.operator) {
                    case 'division':
                      return props.firstNum / prevNum
                      break
                    case 'multiplication':
                      return props.firstNum * prevNum
                      break
                    case 'subtraction':
                      return props.firstNum - prevNum
                      break
                    case 'addition':
                      return props.firstNum + prevNum
                      break
                    default:
                      return prevNum
                      break
                  }

                  props.setFirstNum(0)
                  props.setOperator(undefined)
                  }
                )}
              >=</Button>
              <div id='calc-ops-adv'>
                <Button
                  onClick={() => props.setNumber(prevNum => Math.sqrt(prevNum))}
                >sqrt</Button>
                <Button>%</Button>
                <Button
                  onClick={() => props.setNumber(prevNum => 1 / prevNum)}
                >1/x</Button>
              </div>
            </div>
          </div>
        </WindowContent>
      </Window>
    </ThemeProvider>
  )
}
